        -45deg,
        #c1ab8e,
        #c1ab8e 10px,
        #e74c3c 10px,
        #e74c3c 20px,
        #c1ab8e 20px,
        #c1ab8e 30px,
        #1e90ff 30px,
        #1e90ff 40px
      )
      0 0/100% 5px no-repeat,
    #c1ab8e;
  z-index: 4;
  -webkit-clip-path: polygon(5% 40%, 50% 100%, 95% 40%, 100% 0, 0 0);
          clip-path: polygon(5% 40%, 50% 100%, 95% 40%, 100% 0, 0 0);
}
