  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  background: repeating-linear-gradient(
        -45deg,
        #cebda6,
        #cebda6 10px,
        #e74c3c 10px,
